@use "../../assets/sass/abstracts/variables" as *;
@use "../../assets/sass/base/typography" as *;

.card {
  &__link {
    flex-shrink: 0;
    text-decoration: none;
    display: block;
    border-radius: 12px;
    width: 90%;
    height: 470px;
    font-family: $font-main;
    background-color: $color-secondary;
    margin: 20px auto;
  }
  &__front {
    transform: translateY(0px);
    position: relative;
    height: 100%;
    width: 100%;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background-color: $color-white;
    transition: ease 300ms;
    &:hover {
      transform: translateY(-16px);
    }
  }
  &__image {
    position: relative;
    top: 2px;
    border-radius: 4px;
    padding: 25px;
    align-self: center;
    background: linear-gradient(
      200deg,
      $color-primary--dark,
      $color-primary--dark,
      $color-secondary,
      $color-primary,
      $color-primary--light
    );
    background-size: 400%;
    animation: gradient 5s ease-in-out infinite;
    width: 90%;
    height: 320px;
    object-fit: contain;
  }

  &__text {
    position: relative;
    top: 13px;
    background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
      url("https://img.freepik.com/free-photo/original-black-leather-texture-background_24972-1600.jpg");
    border-radius: 0px 0px 12px 12px;
    width: 100%;
    text-decoration: none;
    color: $color-white;
    height: 80px;
    padding: 65px 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }

  &__title {
    text-shadow: 3px 3px 6px $color-primary--dark;
    font-size: 1.5rem;
    text-transform: uppercase;
    font-family: $font-header;
  }

  &__tagline {
    font-size: 1.2rem;
    text-align: justify;
    color: $color-white;
    font-weight: 500;
    text-shadow: 1px 0px 0px $color-primary--dark;
  }
}

@media screen and (min-width: 425px) {
  .card {
    &__link {
      margin: 20px auto;
      width: 400px;
    }
    width: 100%;
  }
}

@media screen and (min-width: 868px) {
  .card {
    &__link {
      margin: 20px 8px;
      width: 400px;
    }
    width: 100%;
  }
}
